<template>
  <van-pull-refresh v-model="isLoading" @refresh="reFresh">
    <div :class="$style.me">
      <v-mask>
        <v-avatar :avatar="avatar"></v-avatar>
      </v-mask>
      <van-cell-group :border="false" class="van-hairline--bottom">
        <van-cell title="我的收藏" is-link clickable :to="toCollect"></van-cell>
        <van-cell title="更多信息" is-link :to="toMore" clickable></van-cell>
        <van-switch-cell v-model="checked" title="评论加尾" />
      </van-cell-group>
      <div :class="$style.btn">
        <van-button v-if="user" type="danger" size="large" @click="beforeLogOut">注销</van-button>
        <van-button v-else type="primary" size="large" @click="$router.push('/login')">登陆</van-button>
      </div>
    </div>
  </van-pull-refresh>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex'
import { Button, CellGroup, CouponCell, Cell, SwitchCell } from 'vant'

import Mask from '@/components/Mask'
import Avatar from '@/components/Avatar'
export default {
  name: 'x-me',
  components: {
    [Mask.name]: Mask,
    [Avatar.name]: Avatar,
    [Button.name]: Button,
    [CellGroup.name]: CellGroup,
    [CouponCell.name]: CouponCell,
    [Cell.name]: Cell,
    [SwitchCell.name]: SwitchCell
  },
  data () {
    return {
      isLoading: false
    }
  },
  methods: {
    ...mapMutations(['setAddLogo']),
    ...mapActions(['logOut']),
    reFresh () {
      setTimeout(() => {
        this.isLoading = false
      }, 1000)
    },
    async beforeLogOut () {
      try {
        await this.$dialog.confirm({title: '确定注销?', overlay: true, lockScroll: true, message: '点击确认注销！'})
        await this.logOut()
        this.$toast.success({
          mask: true,
          message: '注销成功!',
          duration: 500
        })
      } catch (error) {
        this.$toast.fail({
          mask: true,
          message: '取消注销!',
          duration: 500
        })
        console.log(error)
      }
    }
  },
  computed: {
    ...mapGetters(['user', 'addLogo']),
    avatar () {
      if (this.user) {
        return this.user.avatar_url
      }
      return null
    },
    toMore () {
      if (this.user) {
        return `/user/${this.user.loginname}`
      }
      return ''
    },
    checked: {
      set (v) {
        this.setAddLogo(v)
      },
      get () {
        return this.addLogo
      }
    },
    toCollect () {
      if (this.user) {
        return `/user/${this.user.loginname}/collect`
      }
      return ''
    }
  }
}
</script>

<style module>
.me{
  width: 100%;
  min-height: calc(100vh - 96px);
}
.btn{
  margin-top: 20px;
  padding: 0 20px;
}
</style>



