<template>
  <v-header>
    <div>
      {{title}}
    </div>
  </v-header>
</template>

<script>
import { mapGetters } from 'vuex'
import Header from '@/base/Header'
export default {
  name: 'x-header',
  components: {
    [Header.name]: Header
  },
  computed: {
    ...mapGetters(['user']),
    title () {
      if (this.user) {
        return this.user.loginname
      }
      return '未登录'
    }
  }
}
</script>



